main {
  min-height: 95vh;
}

a {
  color: #222;
}

.loginBg {
  background-image: url("./images/eclasseBanner.jpg");
}

.underlined {
  text-decoration: underline;
}

.container {
  margin: auto;
  max-width: 1100px;
  padding: 8px;
}

.mtop {
  margin-top: 80px !important;
}

.ptop {
  padding-top: 70px !important;
}

.smNavBar svg {
  color: #222 !important;
}

a {
  text-decoration: none;
}

.smNavBar {
  padding: 6px;
}

.smNavBar svg {
  color: #fff !important;
}

.white {
  color: #fff !important;
}

.img-fluid {
  object-fit: cover;
}

.gravatar {
  height: 20px;
  width: 20px;
  border: 1px solid #bbb;
  text-transform: uppercase;
  color: #666;
  font-weight: 600;
  border-radius: 50%;
  padding: 8px;
}

.bg-white {
  background-color: "#fff" !important;
}

.bg-light {
  background-color: "#f1f1f1" !important;
}

.bold {
  font-weight: bold !important;
}

.courseCardSkeleton {
  width: 100%;
  height: 260px;
}

.course-item-loading {
  width: 98%;
  margin: 28px 5px;
  height: 18px;
  background-color: #eee;
}

.menu-items .d-flex:hover {
  background-color: #f3f3f3;
}

.list-icon {
  margin-left: 12px;
  margin-right: 4px;
}

.list-text {
  margin-top: 11px !important;
}

.active-btn ul {
  border-right: 4px solid #003f88;
  color: #003f88;
}

.mr-6 {
  margin-right: 12px;
}

.alert-message {
  position: absolute;
  top: 12%;
  left: 25%;
  z-index: 999 !important;
}

.profile {
  position: fixed;
}

.progress-container {
  margin: 12px 8px;
}

.avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  padding: 6px;
  background-color: #444;
  color: #fff;
  margin-top: 14px, margin-bottom 18;
  text-transform: uppercase;
}

.d-flex {
  display: flex;
}

.justify-content-center {
  justify-content: center;
}

.justify-content-between {
  justify-content: space-between;
}

.align-items-center {
  align-items: center;
}

.showForSmall {
  display: none !important;
}

.table-container {
  width: 100%;
  overflow: auto;
}

@media screen and (max-width: 600px) {
  .dialog {
    margin: auto -34px !important;
  }

  h4 {
    font-size: 1.5rem !important;
  }
}

@media screen and (max-width: 425px) {
  .responsiveText {
    font-size: 13px !important;
    display: inline !important;
  }

  .table-container {
    margin: -14px !important;
    margin-right: -28px !important;
    height: 100vh;
  }

  .hideForSmall {
    display: none !important;
  }

  .showForSmall {
    display: block !important;
  }

  .mtop {
    margin-top: 50px !important;
  }
}
